
import { defineComponent, ref, computed } from 'vue';
import LottieAnimation from 'lottie-vuejs/src/LottieAnimation.vue';
import Tag from '@/components/Tag.vue';
import Project from '@/components/Project.vue';
import projects from '@/projects';
import talks from '@/assets/talks.json';
import hackathons from '@/assets/hackathons.json';
import ProjectTag from '../models/ProjectTag';

export default defineComponent({
  components: {
    Tag,
    Project,
    LottieAnimation,
  },
  setup() {
    const hasClickedFilter = ref(false);

    const clickedFilter = ref('');
    const languages = ref([
      ProjectTag.JavaScript,
      ProjectTag.TypeScript,
      ProjectTag.Go,
      ProjectTag.Python,
      ProjectTag.Java,
      ProjectTag.CSS,
      ProjectTag.SQL,
      ProjectTag.Kotlin,
      ProjectTag.Scala,
      ProjectTag.Dart,
    ]);
    const technologies = ref([
      ProjectTag.Vue,
      ProjectTag.Nuxt,
      ProjectTag.GraphQL,
      ProjectTag.GRPC,
      ProjectTag.Tailwind,
      ProjectTag.Redis,
      ProjectTag.PostgreSQL,
      ProjectTag.Angular,
      ProjectTag.JQuery,
      ProjectTag.Nginx,
      ProjectTag.Kafka,
      ProjectTag.Git,
      ProjectTag.React,
      ProjectTag.Node,
      ProjectTag.Docker,
      ProjectTag.Jenkins,
      ProjectTag.SpringBoot,
      ProjectTag.MySQL,
      ProjectTag.MongoDB,
      ProjectTag.Electron,
      ProjectTag.Firebase,
      ProjectTag.Tensorflow,
      ProjectTag.Scikitlearn,
      ProjectTag.Jupyter,
      ProjectTag.PythonLibs,
      ProjectTag.Vim,
    ]);

    const setClickedFilter = (criterium: string): void => {
      if (!hasClickedFilter.value) {
        hasClickedFilter.value = true;
      }

      if (clickedFilter.value === criterium) {
        clickedFilter.value = '';
      } else {
        clickedFilter.value = criterium;
      }
    };

    const filteredProjects = computed(() => {
      if (!clickedFilter.value) {
        return projects;
      }
      return projects.filter((project) =>
        project.tags.includes(clickedFilter.value as ProjectTag),
      );
    });

    const pdfContent = ref<HTMLElement | null>(null);

    return {
      pdfContent,
      hackathons,
      talks,
      clickedFilter,
      languages,
      technologies,
      setClickedFilter,
      projects,
      filteredProjects,
      hasClickedFilter,
    };
  },
});
